.background {
  background-color: hsl(0, 0%, 98%);
}

@media (min-width: 900px) {
  .desktop-version {
    width: 100vw;
    display: grid;
    grid-template-columns: 7fr 5fr;
    grid-template-rows: 10fr 2fr;
    padding: 0 2rem;
    box-sizing: border-box;
    max-height: calc(100vh - 6rem);
  }
}